import React, { useEffect, useState } from "react";
import "./Overview.scss";
import CardItem from "../CardItem/CardItem";
import data from "../../data";
import Detail from "../../interfaces/Detail";
import { Row, Col } from "antd";

const Overview: React.FC<{ selectedItem: string }> = ({ selectedItem }) => {
  const [cardList, setCardList] = useState<Detail[]>([]);

  useEffect(() => {
    setCardList(data.filter((list) => list.id === selectedItem)[0].details);
  }, [selectedItem]);

  return (
    <Row className="overview" gutter={16}>
      {cardList.map((item: Detail) => (
        <Col>
          <CardItem key={item.id} item={item} />
        </Col>
      ))}
    </Row>
  );
};

export default Overview;
